import React, {useState} from 'react'
import Webcam from 'react-webcam'
import CustomButton from '../custom-button/custom-button.component'
import './image-capture.styles.scss'

const videoConstraints = {
    width: 800,
    height: 800
}



export default function ImageCapture() {

    const [webcam, setWebcam] = useState(null);
    const [classification, setClass] = useState(null);

    const setRef = (webcam) =>{
        if(webcam) setWebcam(webcam);
    }

    const capture = () => {
        const imageSrc = webcam.getScreenshot();
        classifier.classify(webcam, (err, results) => {
            setClass(results[0].label);
          });
    }
    const modelLoaded=()=>{
        console.log('Model Loaded!');
    }

    const ml5 = window.ml5;
    const classifier = ml5.imageClassifier('MobileNet', modelLoaded);
   

    return (
        <div className="image-capture">
            <Webcam
                audio={false}
                width={window.innerHeight/2}
                height={window.innerHeight/2}
                ref={setRef}
                screenshotFormat='image/jpeg'
                videoConstraints={videoConstraints}
            />
             <p>{classification}</p>
            <CustomButton onClick={capture}>Capture</CustomButton> 
        </div>
    )
}

